import './qr-card.css';
import QRCode from 'qrcode-svg';

export default function QrCard({content}) {

    const svg = content !== '' ? new QRCode({
        content: content,
        xmlDeclaration: false,
        padding: 0,
        width : 195,
        height: 195
    }).svg() : null;

  return (
      <div className="container"> 
        <div className="qr-card">
            <div className="qr-code-checkin">
                <div className="qr-code " dangerouslySetInnerHTML={{__html:svg}}/>
                CHECK IN
            </div>
            <div className="description-and-logo">
                <div className="logo"><Logo/></div>
                <div className="description">
                    <div className="desk">
                        <h1>Desk</h1>
                        02-3001.01
                    </div>
                    <div className="location">
                        Bristol Boutique
                        <br></br>
                        Level 2
                    </div>
                </div>
            </div>
            
        </div>
    </div>
  );
}

